import { FC } from "react";
import styled from "styled-components";
import { colorVariables, sizeVariable } from "@/assets/styles/commonStyles.ts";
import { Subtitle } from "@/components/common/subtitle/Subtitle.tsx";

//Types
type Props = {
  text: string;
};

//Styles
const StyledSubtitle = styled(Subtitle)`
  color: ${colorVariables.whiteColor};
  padding-bottom: 2.57vw;
  background: transparent;
  border-radius: 0;
  text-align: center;
  font-size: calc(25vw / 14.4);
  line-height: 1.5;

  @media (max-width: ${sizeVariable}) {
    font-size: calc(25vw / 5.09);
    padding-bottom: 0;
  }
`;

//Component
export const SlideSubtitle: FC<Props> = ({ text }) => {
  return <StyledSubtitle>{text}</StyledSubtitle>;
};
