import Slider, { Settings } from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import styled from "styled-components";
import { FC } from "react";
import { sizeVariable } from "@/assets/styles/commonStyles.ts";
import { ArrowButton } from "@/components/main-page/photo/photo-slider/arrow-button/ArrowButton.tsx";
import { Slide } from "@/components/main-page/photo/photo-slider/slide/Slide.tsx";
import { photoSlideArrows } from "@/assets/constants/constants.ts";

//Types
type Slide = {
  index: string;
  image: string;
  alt: string;
};

type Props = {
  slides: Slide[];
};

//Styles
const StyledSlideContent = styled.div`
  margin: 0;
  padding: 0;
  border-radius: 20px;
  width: calc(100% - 4.58vw);
  height: 100%;

  @media (max-width: ${sizeVariable}) {
    width: calc(100% - 4.72vw);
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 20px;
  }

  @media (max-width: ${sizeVariable}) {
    width: calc(100% - 4.72vw);
  }
`;

const StyledSliderContainer = styled.div`
  width: 100%;
  margin: 0 auto;
  position: relative;

  .slick-track {
    display: flex;
    align-items: flex-end;
  }
`;

//Components
export const PhotoSlider: FC<Props> = ({ slides }) => {
  const settings: Settings = {
    className: "center",
    centerMode: true,
    infinite: true,
    slidesToShow: 1,
    speed: 1000,
    nextArrow: (
      <ArrowButton
        className="arrow next"
        src={photoSlideArrows.right.image}
        alt={photoSlideArrows.right.alt}
      />
    ),
    prevArrow: (
      <ArrowButton
        className="arrow prev"
        prev
        src={photoSlideArrows.left.image}
        alt={photoSlideArrows.left.alt}
      />
    ),
    variableWidth: true,
    initialSlide: 0,
  };

  return (
    <StyledSliderContainer>
      {/*<GlobalStyles />*/}
      <Slider {...settings}>
        {slides.map((slide) => (
          <Slide key={slide.index} index={slide.index}>
            <StyledSlideContent>
              <img src={slide.image} alt={slide.alt} />
            </StyledSlideContent>
          </Slide>
        ))}
      </Slider>
    </StyledSliderContainer>
  );
};
